// import 'firebase/auth'
// import firebaseConfig from './firebaseConfig'
import Attendances from "./pages/Attendances";
import Teachers from "./pages/Teachers";
import Login from "./pages/Login/Login";
import "antd/dist/antd.css";
import { Route, Switch, Redirect } from "react-router-dom";
import CustomMenu from "./components/CustomeMenu";
import CustomHeader from "./components/CustomHeader";
import CustomFooter from "./components/CustomFooter";
import { Layout } from "antd";
import ScheduleManagement from "./pages/ScheduleManagement";
import Dashboard from "./pages/Dashboard";
import io from "socket.io-client";
// import Information from "./pages/Information/Information";

class App extends React.Component {
  state = {
    message: ''
  }
  componentWillMount() {
    // this.socket = io("localhost:3001");
    this.socket = io("http://serverlinhchicken.ga");
    this.socket.on("id", (res) => {

    }); // lng nghe event c tn 'id'
    this.socket.on("newMessage", (response) => {
      this.newMessage(response);
    }); //lng nghe event 'newMessage' v gi hm newMessage khi c event
  }
  //Khi c tin nhn mi, s push tin nhn vo state mesgages, v n s c render ra mn hnh
  newMessage(m) {
    console.log(m);
    this.setState({
      message: this.state.message + m
    })
  }
  //Gi event socket newMessage vi d liu l ni dung tin nhn
  sendnewMessage(m) {
    if (m.value) {
      this.socket.emit("newMessage", m.value); //gi event v server
      m.value = "";
    }
  }
  render() {
    return (
      <div className="App">
        <Switch>
          <Layout style={{ minHeight: "100vh" }}>
            <CustomMenu />
            <Layout>
              <CustomHeader />
              <Switch>
                <Route exact path="/">
                  <Redirect to="/login" />
                </Route>
                <Route path="/login" component={Login} exact />

                <Route path="/attendances" component={Attendances} exact />
                <Route path="/dashboard" component={Dashboard} exact />
                <Route path="/teachers" component={Teachers} exact />
                {/* <Route path="/adminOverview/" component={Admin} exact /> */}
                {/* <Route path="/adminDetail/" component={AdminDetailView} exact /> */}
                {/* <Route path="/information/" component={Information} exact /> */}
                <Route
                  path="/admin/schedule-management"
                  component={ScheduleManagement}
                  exact
                />
              </Switch>
              <CustomFooter />
            </Layout>
          </Layout>
        </Switch>
      </div>
    );
  }
}
export default App;
